.profile-container {
  background-color: #12002b;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.profile-header {
  background-color: #12002b;
  color: #fff;
  padding: 1rem 0;
  text-align: center;
  width: 100%;
  margin-bottom: 1rem;
}

.profile-header h1 {
  font-size: 2.5rem;
  margin: 0;
  padding: 0;
}

.profile-edit-button {
  border-color: #00bc77;
  background-color: #00bc77;
  color: #fff;
  font-weight: bold;
  padding: 10px;
  margin-top: 1rem;
  width: 120px;
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.profile-accounts {
  display: flex;
  flex-direction: column;
  gap: 100px;
  width: 100%;
  margin: 0;
  padding: 0;
}

.profile-account {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  color: #000;
  padding: 1.5rem 2rem;
  margin: 0 100px;
  width: calc(100vw - 250px);
  border-radius: 5px;
  box-sizing: border-box;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: left;
}

.profile-account-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #333;
  margin: 0;
}

.profile-account-amount {
  font-size: 2.5rem;
  font-weight: bold;
  color: #00bc77;
  margin: 0.5rem 0;
}

.profile-account-desc {
  font-size: 1rem;
  color: #555;
  margin: 0;
}

.profile-btn {
  padding: 10px 20px;
  background-color: #00bc77;
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}